<template>
  <div class="lr">
    <div class="lr-box">
      <h2>欢迎回来</h2>
      <van-field
        label="用户名"
        clearable
        maxlength="11"
        v-model="loginId"
        placeholder="请输入手机号"
      ></van-field>
      <van-field
        label="密码"
        :right-icon="closeEye ? 'closed-eye' : 'eye-o'"
        :type="closeEye ? 'password' : 'text'"
        @click-right-icon="onClickRightIcon"
        clearable
        maxlength="6"
        v-model="password"
        placeholder="请输入密码"
      ></van-field>
      <div class="lr-forget">
        <span @click="onForgetPassword">忘记密码</span>
      </div>
      <div class="lr-btn" @click="onClickLogin">
        登录
      </div>
    </div>
  </div>
</template>

<script>
import gql from "graphql-tag";
import { Field, Cell, CellGroup, Image } from "vant";
import { comData } from "@/mix/comData";
export default {
  name: "Login",

  mixins: [comData],

  components: {
    [Field.name]: Field,
    [Cell.name]: Cell,
    [CellGroup.name]: CellGroup,
    [Image.name]: Image
  },

  data() {
    return {
      loginId: "",
      password: "",
      closeEye: true
    };
  },
  methods: {
    onClickRightIcon() {
      this.closeEye = !this.closeEye;
    },

    //忘记密码
    onForgetPassword() {
      this.$router.push({
        path: "/reset"
      });
    },

    async onClickLogin() {
      if (!this.loginId) {
        this.$toast("请输入用户名");
        return;
      }
      if (!this.password) {
        this.$toast("请输入密码");
        return;
      }
      const mutation = gql`
        mutation login($orgId: String!, $loginId: String!, $password: String!) {
          member {
            login(
              orgId: $orgId
              loginId: $loginId
              password: $password
            ) {
              mobile
              orgId
              token
              uid
            }
          }
        }
      `;
      const result = await this.$apollo.mutate({
        mutation: mutation,
        variables: {
          orgId: this.orgId,
          loginId: this.loginId,
          password: this.password
        }
      });
      let {
        data: {
          member: {
            login: { uid, token }
          }
        }
      } = result;
      this.$store.commit("SET_TOKEN", token);
      this.$store.commit("SET_UID", uid);
      sessionStorage.setItem("token", token);
      sessionStorage.setItem("uid", uid);
      this.$router.push({
        path: "/index",
        query:{mallId:this.mallId}
      });
    }
  }
};
</script>

<style lang="less" scoped>
@import url("../assets/css/login.less");
</style>
